import { PageRendererProps, navigate } from 'gatsby';
import React, { useEffect } from 'react';

const IndexPage: React.FC<PageRendererProps> = () => {
  useEffect(() => {
    navigate('/en/posts', {
      replace: true,
    })
      .catch((e) => {
        console.error(e);
      });
  }, []);

  return null;
};
export default IndexPage;
